import axios from 'axios';

axios.interceptors.request.use( //
        config => {
                return config;
        },
        error => {
                return Promise.reject(error);
        }
);
axios.interceptors.response.use( //
        result => {
                return result.data;
        },
        error => {
                return Promise.reject(error);
        }
);
let http = {},
        contentTypes = {get: 'application/x-www-form-urlencoded', post: 'application/json'},
        alwaysPendingPromise = new Promise(() => {
        }); // promise
['get', 'post'].forEach(item => {
        http[item] = (url, params) => {
                params = item === 'get'?{params: params}:{data: params};
                let config = {
                        withCredentials:true,   //cookie
                        timeout: 5000,             // axiosajax
                        headers: {'Content-Type': contentTypes[item]},
                        method: item === 'get'?item:'post',
                        url,
                        ...params,
                };
                return axios(config)
                        .then(({status, message, data}) => {
                                if(message) alert(message);
                                switch(status){
                                        case 200:                       // 
                                                return data;
                                        case 999:                       // 
                                                alert('');
                                        default:                         // 
                                                return alwaysPendingPromise;
                                }
                        })
                        .catch(error => {
                                alert(error);
                                return alwaysPendingPromise;
                        })
        }
});
export default http;


